body {
  position: relative;
  min-width: 320px;
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  font-family: "Roboto", "Arial", sans-serif;
  transition: 0.5s;
}

*::-webkit-scrollbar {
  width: 5px;
  height: 5px;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #6320EE;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

@font-face {
  font-display: swap;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  src: url("../fonts/roboto-300.woff2") format("woff2"), url("../fonts/roboto-300.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-700.woff2") format("woff2"), url("../fonts/roboto-700.woff") format("woff");
}
.dark .button:hover, .dark .button:focus {
  box-shadow: 0 0 10px 5px rgba(1, 151, 246, 0.9);
}
.dark .button:focus {
  outline-color: #0197F6;
}
.dark .button:active {
  box-shadow: 0 0 10px 2px #0197f6;
}
.dark .control__line {
  background-color: #0197F6;
}
.dark .control__line::-webkit-progress-value {
  background-color: #0197F6;
}
.dark .control__range::-webkit-slider-runnable-track {
  box-shadow: 0 0 3px 1px rgba(1, 151, 246, 0.9);
}
.dark .control__range::-webkit-slider-thumb {
  background-color: #0197F6;
}
.dark .control__icon {
  fill: #0197F6;
}
.dark .footer__theme--active {
  box-shadow: 0 0 5px 1px rgba(1, 151, 246, 0.6);
}
.dark .footer__icon {
  fill: #0197F6;
  stroke: #0197F6;
}
.dark .footer__copyright {
  color: #0197F6;
}
.dark .footer__copyright--name {
  color: #FFFFFF;
}
.dark .header__logo path:first-of-type {
  fill: #0197F6;
}
.dark .header__logo path:last-of-type {
  fill: #FFFFFF;
}
.dark .header__icon {
  fill: #0197F6;
}
.dark .main {
  background-color: #02182B;
}
.dark .settings {
  background-color: rgba(2, 24, 43, 0.8);
}
.dark .settings__icon {
  fill: #0197F6;
}
.dark .video {
  background-color: #364652;
}
.dark .video__wrapper {
  box-shadow: 0 0 15px 5px rgba(1, 151, 246, 0.9);
}
.dark .video__wrapper--error {
  box-shadow: 0 0 15px 5px rgba(243, 80, 80, 0.9);
}
.dark .video__icon path {
  stroke: #0197F6;
}
.dark .statistics {
  color: #0197F6;
}

.header {
  position: absolute;
  top: 0;
  left: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  padding: 25px;
  transform: translateX(-50%);
}

.header__logo {
  width: 150px;
  height: 45px;
  transition: 0.3s;
  transition-delay: 0.2s;
}
.header__logo path {
  transition: 0.3s;
  transition-delay: 0.2s;
}

.header__menu {
  width: 45px;
  height: 45px;
  transition: 0.2s;
}
.header__menu--error {
  animation-name: shake;
  animation-duration: 0.1s;
  animation-iteration-count: 11;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  animation-direction: alternate;
}
.header__menu--error .header__icon {
  fill: #F35050;
}

@keyframes shake {
  0% {
    transform: translateX(-10%);
  }
  100% {
    transform: translateX(0%);
  }
}
.header__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 28px;
  height: 28px;
  transform: translate(-50%, -50%);
  fill: #8075FF;
}

@media (min-width: 768px) {
  .header__logo {
    width: 180px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding: 50px;
  }
  .header__logo {
    width: 250px;
  }
  .header__menu {
    width: 60px;
    height: 60px;
  }
  .header__icon {
    width: 40px;
    height: 40px;
  }
}
.button {
  position: relative;
  box-sizing: border-box;
  display: block;
  margin: 0;
  padding: 0;
  font-family: inherit;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
  outline: none;
  transition: 0.2s;
}
.button:hover, .button:focus {
  box-shadow: 0 0 10px 1px rgba(99, 32, 238, 0.9);
}
.button:focus {
  outline: 1px solid #6320EE;
}
.button:active {
  box-shadow: 0 0 10px 2px #6320ee;
}

.settings {
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100%;
  padding: 30px 15px;
  color: #FFFFFF;
  background-color: rgba(128, 117, 255, 0.5);
  backdrop-filter: blur(5px);
  outline: none;
  overflow-y: scroll;
  transition: 0.5s;
  z-index: 3;
}
.settings--hide {
  opacity: 0;
  transform: translateY(-100%);
}

.settings__close {
  position: absolute;
  bottom: 25px;
  right: 20px;
  width: 45px;
  height: 45px;
  z-index: 3;
}

.settings__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30px;
  height: 30px;
  transform: translate(-50%, -50%);
  fill: #6320EE;
}

.settings__wrapper {
  position: relative;
  box-sizing: border-box;
  width: 100%;
}
.settings__wrapper--hide {
  display: none;
}

.settings__control {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.settings__control--hide {
  display: none;
}

.settings__button {
  margin-right: 10px;
  margin-bottom: 10px;
  padding: 10px 15px;
  color: #FFFFFF;
  font-size: 16px;
  font-weight: 300;
  border: 1px solid #FFFFFF;
  border-radius: 30px;
  box-shadow: none;
  transition: 0.2s;
}
.settings__button:last-child {
  margin-right: 0;
}
.settings__button:hover, .settings__button:focus {
  outline: none;
  box-shadow: 0 0 10px 3px rgba(255, 255, 255, 0.9);
}
.settings__button:active {
  box-shadow: 0 0 10px 6px rgba(255, 255, 255, 0.9);
}
.settings__button--active {
  box-shadow: 0 0 10px 5px rgba(255, 255, 255, 0.9);
}

.settings__tab {
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  transform: translateY(-50%) scale(0);
  transition: 0.2s;
}
.settings__tab--active {
  transform: translateY(-50%) scale(1);
}

.settings__column {
  margin-bottom: 30px;
}
.settings__column:last-child {
  margin-bottom: 0;
}

.settings__details {
  text-align: center;
}

.settings__summary {
  display: inline-block;
  cursor: pointer;
}
.settings__summary::-webkit-details-marker {
  display: none;
}

.settings__label {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  cursor: pointer;
  transition: 0.2s;
}
.settings__label:last-child {
  margin-bottom: 0;
}
.settings__label--hide {
  position: relative;
  margin-top: -18px;
  margin-bottom: 0;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
}

.settings__checkbox {
  width: 15px;
  height: 15px;
  margin: 0;
  margin-right: 10px;
}
.settings__checkbox:disabled {
  cursor: not-allowed;
}

.settings__name {
  font-size: 15px;
  font-weight: 300;
}

.settings__sub {
  margin: 0;
  margin-bottom: 20px;
  font-size: 25px;
  font-weight: 700;
  text-align: center;
}

.settings__time {
  display: block;
  margin-bottom: 10px;
  font-size: 13px;
  font-weight: 700;
}

.settings__text {
  margin-top: 0;
  margin-bottom: 30px;
  font-weight: 300;
}
.settings__text--tiny {
  padding: 0 20px;
}

.settings__list {
  margin: 0;
  padding: 0;
}

.settings__item {
  margin-bottom: 8px;
  font-weight: 300;
  list-style: inside;
}
.settings__item:last-child {
  margin-bottom: 0;
}

.settings__video {
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 15px;
  padding: 10px 15px;
  color: #FFFFFF;
  font-size: 15px;
  font-weight: 300;
  border: 1px solid #FFFFFF;
  border-radius: 30px;
}
.settings__video:last-child {
  margin-bottom: 0;
}

.settings__file {
  display: inline-block;
  max-width: 150px;
  margin: 0 auto;
  padding: 10px 15px;
  color: #FFFFFF;
  font-size: 15px;
  font-weight: 300;
  font-family: inherit;
  border: 1px solid #FFFFFF;
  border-radius: 30px;
}
.settings__file::-webkit-file-upload-button {
  display: none;
}

@media (min-width: 768px) {
  .settings {
    overflow-y: unset;
  }
  .settings__close {
    top: 25px;
    right: 25px;
    left: auto;
    width: 45px;
    height: 45px;
  }
  .settings__icon {
    width: 33px;
    height: 33px;
  }
  .settings__control {
    padding-right: 50px;
  }
  .settings__button {
    margin-right: 15px;
    margin-bottom: 15px;
    font-size: 18px;
  }
  .settings__label {
    justify-content: center;
    margin-bottom: 15px;
  }
  .settings__label--hide {
    margin-top: -37px;
  }
  .settings__name {
    font-size: 18px;
  }
  .settings__sub {
    font-size: 30px;
  }
  .settings__text {
    font-size: 18px;
  }
  .settings__item {
    font-size: 18px;
  }
  .settings__video {
    padding: 15px 25px;
    font-size: 18px;
  }
  .settings__file {
    max-width: 185px;
    padding: 15px 20px;
    font-size: 18px;
  }
}
@media (min-width: 1440px) {
  .settings {
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .settings__close {
    top: 50px;
    right: 50px;
    width: 60px;
    height: 60px;
  }
  .settings__icon {
    width: 40px;
    height: 40px;
  }
  .settings__control {
    width: 40%;
    margin-right: 30px;
    padding-right: 0;
  }
  .settings__button {
    padding: 15px 25px;
    font-size: 25px;
  }
  .settings__wrapper {
    width: 60%;
  }
  .settings__label {
    margin-bottom: 15px;
  }
  .settings__label--hide {
    margin-top: -45px;
  }
  .settings__name {
    font-size: 25px;
  }
  .settings__sub {
    margin-bottom: 30px;
    font-size: 40px;
  }
  .settings__text {
    font-size: 25px;
  }
  .settings__item {
    font-size: 25px;
  }
  .settings__video {
    font-size: 25px;
  }
  .settings__file {
    max-width: 240px;
    font-size: 25px;
  }
}
.main {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  color: #FFFFFF;
  background-color: #F8F0FB;
  transition: 0.5s;
}

.video {
  box-sizing: border-box;
  display: block;
  width: 100%;
  height: 100%;
  border-radius: 10px;
  background-color: rgb(245, 246, 247);
  outline: none;
  object-fit: cover;
  transition: 0.2s;
}

.video__wrapper {
  position: relative;
  box-sizing: border-box;
  width: 100%;
  margin: 0 25px;
  box-shadow: 0 0 15px 5px rgba(99, 32, 238, 0.5);
  border-radius: 10px;
  overflow: hidden;
  transition: 0.3s;
  transform: perspective(1000px) rotateY(0deg) scaleZ(2);
  transform-style: preserve-3d;
  z-index: 2;
}
.video__wrapper:hover .control {
  transform: translateY(0);
}
.video__wrapper:hover .statistics {
  transform: translateY(0);
}
.video__wrapper:hover .statistics__ufh {
  transform: translateY(0);
}
.video__wrapper--waiting {
  animation-name: load;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: both;
}
.video__wrapper--error {
  box-shadow: 0 0 15px 5px rgba(243, 80, 80, 0.5);
}
.video__wrapper--pause {
  animation-name: pause;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
  animation-direction: alternate;
  animation-fill-mode: both;
}

@keyframes load {
  100% {
    box-shadow: 0 0 15px 5px #5be462;
  }
}
@keyframes pause {
  100% {
    box-shadow: 0 0 15px 5px #6320ee;
  }
}
.video__start {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  outline: none;
  border-radius: 10px;
  transition: 0.2s;
  z-index: 1;
}
.video__start--hide {
  background-color: transparent;
  opacity: 0;
  z-index: -1;
}
.video__start::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(99, 32, 238, 0.2);
}
.video__start:hover .video__icon, .video__start:focus .video__icon {
  transform: translate(-50%, -50%) scale(1.2);
}
.video__start:hover .video__icon path, .video__start:focus .video__icon path {
  opacity: 1;
}
.video__start:active .video__icon {
  transform: translate(-50%, -50%) scale(0.9);
}

.video__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.2s;
}
.video__icon path {
  opacity: 0.6;
  stroke: #8075FF;
  transition: 0.2s;
}

.video__waiting {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30px;
  height: 30px;
  background-color: #5be462;
  transform: translate(-50%, -50%);
  transition: 0.2s;
  animation-name: waiting;
  animation-duration: 1s;
  animation-iteration-count: infinite;
}
.video__waiting--hide {
  opacity: 0;
  pointer-events: none;
}

@keyframes waiting {
  100% {
    transform: translate(-50%, -50%) rotate(90deg);
  }
}
.video__background {
  display: none;
}

@media (min-width: 425px) {
  .video__wrapper {
    max-width: 500px;
  }
}
@media (min-width: 768px) {
  .video__wrapper {
    max-width: 700px;
  }
  .video__icon {
    width: 150px;
    height: 150px;
  }
}
@media (min-width: 1440px) {
  .video__wrapper {
    width: 1000px;
    max-width: none;
  }
  .video__icon {
    width: 300px;
    height: 300px;
  }
}
.statistics {
  position: absolute;
  top: 0;
  right: 0;
  box-sizing: border-box;
  padding: 10px 15px;
  color: #8075FF;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 10px;
  background-color: rgba(33, 26, 29, 0.5);
  transition: 0.6s;
  transform: translateY(-100%);
}
.statistics--hide {
  opacity: 0;
  transform: translateY(0);
  z-index: -1;
}
.statistics--off {
  display: none;
}

.statistics__category {
  margin: 0;
  margin-bottom: 5px;
  font-size: 10px;
}
.statistics__category:last-child {
  margin-bottom: 0;
}
.statistics__category span {
  color: #FFFFFF;
  font-weight: 300;
}
.statistics__category--hide {
  display: none;
}

.statistics__ufh {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 35px;
  height: 23px;
  background-color: rgba(33, 26, 29, 0.5);
  opacity: 0.8;
  transition: 0.6s;
  transform: translateY(-150%);
}

@media (min-width: 768px) {
  .statistics__category {
    font-size: 13px;
  }
  .statistics__ufh {
    position: absolute;
    top: 15px;
    left: 15px;
    width: 45px;
    height: 30px;
  }
}
@media (min-width: 1440px) {
  .statistics {
    padding: 20px 30px;
  }
  .statistics__category {
    margin-bottom: 10px;
    font-size: 20px;
  }
  .statistics__ufh {
    top: 25px;
    left: 25px;
    width: 80px;
    height: 52px;
  }
}
.control {
  position: absolute;
  bottom: 0;
  left: 0;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  padding: 10px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 5px;
  background-color: rgba(33, 26, 29, 0.5);
  transform: translateY(105%);
  transition: 0.6s;
  z-index: 1;
}
.control--hide {
  opacity: 0;
  transform: translateY(0);
  z-index: -1;
}
.control--off {
  display: none;
}
.control:focus-within {
  transform: translateY(0);
}

.control__line {
  position: absolute;
  top: -10px;
  left: 0;
  width: 0%;
  height: 3px;
  margin: 0 5px;
  border-radius: 5px;
  background-color: #6320EE;
  opacity: 0.7;
  transition: 0.1s;
}
.control__line--hide {
  display: none;
}
.control__line::-webkit-progress-bar {
  border-radius: 5px;
}
.control__line::-webkit-progress-value {
  border-radius: 5px;
  background-color: #6320EE;
}

.control__button {
  order: 2;
  width: 30px;
  height: 30px;
  margin-right: 10px;
  transition: 0.2s;
}
.control__button:last-child {
  margin-right: 0;
}

.control__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  transition: 0.2s;
  fill: #6320EE;
}
.control__icon--hide {
  display: none;
}

.control__mute {
  opacity: 0;
}

.control__duration {
  position: relative;
  display: flex;
  align-items: center;
  flex-grow: 1;
  order: 1;
  width: 100%;
  margin: 0;
  margin-bottom: 5px;
}

.control__time {
  margin-right: 10px;
  font-size: 11px;
  font-weight: 300;
}
.control__time:last-child {
  margin-right: 0;
}

.control__range {
  appearance: none;
  position: relative;
  flex-grow: 1;
  height: 5px;
  margin-right: 10px;
  border-radius: 5px;
  transition: 0.2s;
}
.control__range::-webkit-slider-runnable-track {
  width: 100%;
  height: 5px;
  box-shadow: 0 0 3px 1px rgba(99, 32, 238, 0.9);
  border-radius: 5px;
  background-color: #FFFFFF;
}
.control__range::-webkit-slider-thumb {
  appearance: none;
  width: 15px;
  height: 15px;
  margin-top: -6px;
  border-radius: 50%;
  background-color: #6320EE;
  transition: 0.2s;
  cursor: pointer;
}
.control__range::-moz-range-track {
  appearance: none;
  width: 100%;
  height: 3px;
  box-shadow: 0 0 3px 1px rgba(99, 32, 238, 0.9);
  border-radius: 5px;
  background-color: #FFFFFF;
  cursor: pointer;
}
.control__range::-moz-range-thumb {
  appearance: none;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: #6320EE;
  transition: 0.2s;
  cursor: pointer;
}

@media (min-width: 425px) {
  .control {
    flex-wrap: nowrap;
  }
  .control__button {
    order: inherit;
  }
  .control__duration {
    order: inherit;
    width: auto;
    margin-right: 10px;
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .control {
    padding: 15px;
  }
}
@media (min-width: 1440px) {
  .control {
    padding: 15px 20px;
  }
  .control__button {
    width: 55px;
    height: 55px;
    margin-right: 15px;
  }
  .control__icon {
    width: 30px;
    height: 30px;
  }
  .control__icon--volume {
    width: 35px;
    height: 35px;
  }
  .control__time {
    font-size: 16px;
  }
  .control__range {
    height: 8px;
  }
  .control__range::-webkit-slider-runnable-track {
    height: 8px;
  }
  .control__range::-webkit-slider-thumb {
    width: 23px;
    height: 23px;
    margin-top: -8px;
  }
}
.footer {
  position: absolute;
  bottom: 0;
  left: 50%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  margin: 0 auto;
  width: 100%;
  padding: 25px;
  transform: translateX(-50%);
}

.footer__controls {
  display: flex;
  align-items: center;
  margin: 0;
}

.footer__theme {
  width: 45px;
  height: 45px;
  margin-right: 15px;
  transition: 0.2s;
}
.footer__theme:last-child {
  margin-right: 0;
}
.footer__theme--active {
  box-shadow: 0 0 5px 1px rgba(99, 32, 238, 0.6);
  cursor: not-allowed;
}

.footer__icon {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 30px;
  height: 30px;
  transform: translate(-50%, -50%);
  fill: #8075FF;
  stroke: #8075FF;
}

.footer__copyright {
  color: #8075FF;
  font-size: 15px;
  font-weight: 300;
  text-align: right;
  transition: 0.1s;
}
.footer__copyright--name {
  padding: 0;
  color: #4287F5;
  font-size: inherit;
  border: none;
  background-color: transparent;
  cursor: not-allowed;
}

@media (min-width: 768px) {
  .footer__copyright {
    font-size: 16px;
  }
}
@media (min-width: 1440px) {
  .footer {
    padding: 50px;
  }
  .footer__theme {
    width: 60px;
    height: 60px;
  }
  .footer__icon {
    width: 40px;
    height: 40px;
  }
  .footer__copyright {
    font-size: 22px;
  }
}